<template>
    <div>
        <aplayer v-if="isShow" autoplay :music="musicList[0]" :list="musicList" :showLrc="true" repeat='repeat-one' :float='true'/>
    </div>
</template>
<script>
import Aplayer from 'vue-aplayer'
export default {
  components:{
    'aplayer':Aplayer
  },
  data(){
    return{
      musicList:[],
      isShow:false
    }
  },
  created(){
    let obj = {
      title:"音乐",
      className:"music"
    }
    this.$emit("changeNav",obj)
    this.getData()
  },
  methods: {
    getData(){
      axios.get("./data/musicdata.json")
      .then((res)=>{
        console.log(res)
        let arr = res.data.musicData;
        arr.forEach((element,index) => {
          arr[index].lrc=location.origin+location.pathname+element.lrc
        });
        this.musicList = arr;
        this.isShow = true
      })
    }
  }
}
</script>